import React, { Component } from 'react';
import { useTranslation, withTranslation, Trans } from 'react-i18next';
import UnityDescription from "../soft/unityDescription/UnityDescription";
import Service from "../soft/service/Service";
import Technology from "../soft/technology/Technology";
import PortfolioCompleted from "../soft/portfolioCompleted/PortfolioCompleted";
import PortfolioSchool from "./portfolioSchool/PortfolioSchool";
import Event from "../soft/event/Event";
import RedesUnity from "../soft/redesUnity/RedesUnity"
import './School.css';
import schoolData from 'data/unit/School.json';

class School extends Component {

  render() {

    const { t, i18n } = this.props;

    return (
      <div className="School">
        <UnityDescription unity={schoolData} />
        <p style={{ color: schoolData.colorTitles }} className="School-title-services">{t('unit.services')}</p>
        <div className="School-container-services">
          <img className="School-background11-services" src={"https://drive.google.com/uc?id=18iTCpxd1JlVX4AiBGl-l0Kwe1ds0owVI"} alt={"background11"}></img>
          <div className="School-container-background2-services">
            <img className="School-background21-services" src={"https://drive.google.com/uc?id=17foQMQDy9JIIE7t1Q5NWdLW_jdnRmKOM"} alt={"background21"}></img>
          </div>
          {
            schoolData.services.map(
              (service, index) => {
                return <div key={index} className="School-container-service"> 
                <Service 
                
                service={service} 
                color={schoolData.color} 
                colorTitle={schoolData.colorSubTitles} 
                marcoServices={schoolData.marcoServices} 
                /> 
                </div>
              }
            )
          }
        </div>
        {
          schoolData.technologies.map(
            (index) => {
              if(index === 0){
                return <p style={{ color: schoolData.colorTitles }} className="School-title-services"><Trans i18nKey="unit.techs">technologies<br />used by us</Trans></p>
              }
            }
          )
        }
        <div className="School-container-technologies">
          {
            schoolData.technologies.map(
              (technology, index) => {
                return <Technology key={index} technology={technology} color={schoolData.color} />
              }
            )
          }
        </div>
        <p style={{ color: schoolData.colorTitles }} className="School-title-services">{t('unit.portfolio')}</p>
         <div className="School-container-portfolioCompleted">
          {
            schoolData.portfolio.completed.map(
              (portfolioCompleted, index) => {
                if(index === 0){
                return  <>
                        <div className="School-container-background1-portfolioCompleted">
                          <img className="School-background1-portfolioCompleted" src={"https://drive.google.com/uc?id=1t7KwSZdS2KizI9bVon-RL_lvwV5xPJ39"} alt={"backPortafolio1"}></img>
                        </div>
                        <img className="School-background2-portfolioCompleted" src={"https://drive.google.com/uc?id=124mL9lUu2d-DbJTfvponggcOD12qDHc0"} alt={"backPortafolio2"}></img>
                        <p style={{ color: schoolData.colorTitles }} className="Science-completed-title">{t('unit.completed')}</p>
                        <PortfolioCompleted 
                        key={index} 
                        index={index} 
                        portfolioCompleted={portfolioCompleted} 
                        color={schoolData.color} 
                        colorTitle={schoolData.colorSubTitles} 
                        marcoServices={schoolData.marcoServices}
                        />
                        </>
                }
                else{
                    return <PortfolioCompleted 
                    key={index} 
                    index={index} 
                    portfolioCompleted={portfolioCompleted} 
                    color={schoolData.color} 
                    colorTitle={schoolData.colorSubTitles} 
                    marcoServices={schoolData.marcoServices}
                    />
                }
              }
            )
          }
        </div>
        <div className="School-container-portfolioOngoing">
          {
            schoolData.portfolio.ongoing.map(
              (portfolioOngoing, index) => {
                return <PortfolioSchool
                key={index} 
                index={index} 
                portfolioOngoing={portfolioOngoing} 
                color={schoolData.color} 
                colorTitle={schoolData.colorSubTitles} 
                />
              }
            )
          }
        </div>
        <p style={{ color: schoolData.colorTitles }} className="School-title-services">{t('unit.events')}</p>
        <div className="School-container-events">
          {
            schoolData.events.map(
              (event, index) => {
                return <Event 
                key={index} 
                event={event} 
                color={schoolData.color} 
                marcoServices={schoolData.marcoServices}
                />
              }
            )
          }
        </div>
        <RedesUnity redes={schoolData.redes}/>
      </div>
    );
  }
}

export default withTranslation()(School);
